import { useEffect } from 'react';
import { Container, ListItem, List, Card, Button, ListItemText, LinearProgress } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Page from 'src/components/Page';
import { useDispatch, useSelector } from 'src/store';
import { retrieveTransaction } from 'src/store/slices/slate/transaction';
import { useParams, Link } from 'react-router-dom'

const TransactionDetails = () => {
  const dispatch = useDispatch();

  const { id } = useParams();

  const { currentTransaction, loading } = useSelector((state) => state.slate.transaction);

  useEffect(() => {
    dispatch(retrieveTransaction(id));
  }, []);

  return (
    <Page title="Transaction Details - Block Explorer">
      <Container maxWidth="lg" sx={{ pt: 2 }}>
        <Button component={Link} to="/block-explorer/transactions" startIcon={<ArrowBackIcon />}>
          Transactions
        </Button>
        {loading && <LinearProgress />}
        <Card sx={{ mb: 2 }}>
          <List dense={false}>
            <ListItem secondaryAction={currentTransaction?.id}>
              <ListItemText primary="Transaction" />
            </ListItem>
            <ListItem secondaryAction={currentTransaction?.height}>
              <ListItemText primary="Present in Block" />
            </ListItem>
            <ListItem secondaryAction={currentTransaction?.time}>
              <ListItemText primary="Time" />
            </ListItem>
            <ListItem secondaryAction={currentTransaction?.value_in}>
              <ListItemText primary="Value In" />
            </ListItem>
            <ListItem secondaryAction={currentTransaction?.value_out}>
              <ListItemText primary="Value Out" />
            </ListItem>
            <ListItem secondaryAction={currentTransaction?.coinbase ? 'Yes' : 'No'}>
              <ListItemText primary="Is Coinbase" />
            </ListItem>
          </List>
        </Card>
      </Container>
    </Page>
  );
};

export default TransactionDetails;
